import React, { Component } from 'react';
import './ListItem.css';
import FlipMove from 'react-flip-move';

function ListIteams(props) {
    const items = props.items;
    const ListIteams = items.map(item =>{
        return <div className="list" key={item.key}>
                <input type="text" value={item.text} id={item.key} onChange={(e) =>{
                    props.updateInput(e.target.value, item.key)
                }} />
                <span onClick={() => props.deleteItem(item.key)}>Del</span>
        </div>
    })
    return (
        <div>
            <FlipMove duration={500} easing="ease-in-out">
                {ListIteams}
            </FlipMove>
        </div>
    )
}
 
export default ListIteams;


