import React, { Component } from 'react';
import './App.css';
import ListItems from '../src/ListIteams';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            items: [],
            currentItems: {
                text:'',
                key:''
            },
            textValid: true
         }
        this.handleInput = this.handleInput.bind(this);
        this.addIteam = this.addIteam.bind(this);
        this.deleteItem = this.deleteItem.bind(this);
        this.updateInput = this.updateInput.bind(this);
    }

    handleInput = (e) => {
        const val = e.target.value ? false : true;
        return (
            this.setState({
                currentItems: {
                    text: e.target.value,
                    key:Date.now()
                },
                textValid: val
            })
        )
    }

    addIteam (e) {
        e.preventDefault();
        const newItem = this.state.currentItems;
        if(newItem !=="") {
            const newItems =[...this.state.items, newItem];
            this.setState({
                items: newItems,
                currentItems: {
                    text:'',
                    key:''
                }
            })
        }
        console.log(this.state);
    }
    deleteItem(key) {
       const deletedItems = this.state.items.filter(item => item.key!==key);
       this.setState({
           items: deletedItems
       })
    }
    updateInput(text, key){
    const items = this.state.items;
    items.map(item=>{
        if(item.key==key){
            item.text=text;
        }
    })
    this.setState({
        items:items
    })
    }
  render() {
    return (
      <div className="App">
      <header>
        <form id="to-do-forms" onSubmit={this.addIteam}>
            <input type="text" value={this.state.currentItems.text} placeholder="Add task" onChange={this.handleInput} />
            <button type="submit" disabled={this.state.textValid}>ADD</button>
        </form>
        </header>
        <ListItems items={this.state.items} deleteItem={this.deleteItem} updateInput={this.updateInput}/>
      </div>
    );
  }
}

export default App;



